@import url("https://fonts.googleapis.com/css2?family=Anta&display=swap");
.anta-regular {
  font-family: "Anta", sans-serif;
  font-weight: 400;
  font-style: normal;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Anta", sans-serif;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.title{
  background-color: #d0dff7;
  color:#a0834c;
  padding: 1.5rem;
  text-align: center;
  font-size: 3rem;
}
.container{
  background-color: #dadee4;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  padding: 2rem;
  gap: 1rem;
}
.gallery{
  border:3px solid rgba(0, 0, 255, 0.308);
  border-radius: 10px;
  width: 280px;
  height: 350px;
  overflow: hidden;
}
#image{
  width: 100%;
  height: 300px;
  object-fit: cover;
  transition: 0.5s ease;
  &:hover{
    cursor: pointer;
    opacity: 0.5;
    transform: scale(1.1);
  }
}
.name{
  color: #a0834c;
  text-align: center;
  font-size: 1.1rem;
  padding: 0.5rem
}
#image:hover + .name{
  background-color: orange;
}
